import * as React from "react";
import AppBar from "@mui/material/AppBar";
import Box from "@mui/material/Box";
import Toolbar from "@mui/material/Toolbar";
import Typography from "@mui/material/Typography";
import Button from "@mui/material/Button";
import IconButton from "@mui/material/IconButton";
import DashboardIcon from "@mui/icons-material/Dashboard";
import AccountCircleIcon from "@mui/icons-material/AccountCircle";
import HomeIcon from '@mui/icons-material/Home';
import { useDispatch, useSelector } from "react-redux";
import { logout } from "../ReduxSlices/userSlice";
export default function Navbar() {

  const isAuthenticated= useSelector(state=>state.user.isAuthenticated)
  const dispatch = useDispatch()
  return (
    <Box sx={{ flexGrow: 1 }}>
      <AppBar position="static">
        <Toolbar className="container">
          <IconButton
            size="large"
            edge="start"
            color="inherit"
            aria-label="menu"
            sx={{ mr: 2 }}
          >
            <DashboardIcon />
          </IconButton>
          <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
            Library Management System
          </Typography>
          {/* <div className="mx-3 d-flex align-items-center cursor-pointer fw-bold" >
            <HomeIcon className="me-2"/>
            Home
          </div>
          <div  className="mx-3 d-flex align-items-center cursor-pointer  fw-bold ">
            <AccountCircleIcon className="me-2"/>
            Login
          </div>
          <div  className="mx-3 d-flex align-items-center cursor-pointer  fw-bold ">
            <AccountCircleIcon className="me-2"/>
            {JSON.stringify(isAuthenticated)}
          </div> */}
          {isAuthenticated?
            <div className="mx-3 d-flex align-items-center cursor-pointer fw-bold" onClick={()=>{
            dispatch(logout())
          }}>
            Logout
          </div>
          :null
          }
        </Toolbar>
      </AppBar>
    </Box>
  );
}
