import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import { getCookie, setCookie, eraseCookie } from "../CookieHandler";

export const register = createAsyncThunk(
  "users/register",
  async ({ name, phone, email, password,address,college }, thunkAPI) => {
    try {
      const res = await fetch(
        `${window.configuration.API_URL}api/users/register/`,
        {
          method: "POST",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
          body: JSON.stringify({
            name,
            phone,
            address,
            college,
            email,
            password,
          }),
        }
      );
      const data = await res.json();

      if (res.status === 201) {
        
        return data;
      } else {
        return thunkAPI.rejectWithValue(data);
      }
    } catch (err) {
      return thunkAPI.rejectWithValue(err.response.data);
    }
  }
);

export const login = createAsyncThunk(
  "users/login",
  async ({ email, password }, thunkAPI) => {
    try {
      const res = await fetch(`${window.configuration.API_URL}api/token/`, {
        method: "POST",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          email,
          password,
        }),
      });
      const data = await res.json();

      if (res.status === 200) {
        setCookie("access", data.access, 1);
        setCookie("refresh", data.refresh, 1);

        return data;
      } else {
        return thunkAPI.rejectWithValue(data);
      }
    } catch (err) {
      return thunkAPI.rejectWithValue(err.response.data);
    }
  }
);

export const getUser = createAsyncThunk("users/me", async ({}, thunkAPI) => {
  try {
    const access = getCookie("access");
    const res = await fetch(`${window.configuration.API_URL}api/users/me/`, {
      method: "GET",
      headers: {
        Accept: "application/json",
        Authorization: `Bearer ${access}`,
      },
    });
    const data = await res.json();

    if (res.status === 200) {
      return data;
    } else {
      return thunkAPI.rejectWithValue(data);
    }
  } catch (err) {
    return thunkAPI.rejectWithValue(err.response.data);
  }
});
const initialState = {
  isAuthenticated: false,
  user: null,
  loading: false,
  registered: false,
};

const userSlice = createSlice({
  name: "user",
  initialState,
  reducers: {
    resetRegistered: (state) => {
      state.registered = false;
    },
    logout:(state)=>{
        eraseCookie('access')
        eraseCookie('refresh')
        state.isAuthenticated =false
        state.user = null
        state.loading = false
        state.registered = false
    }
  },
  extraReducers: (builder) => {
    builder
      .addCase(register.pending, (state) => {
        state.loading = true;
      })
      .addCase(register.fulfilled, (state) => {
        state.loading = false;
        state.registered = true;
      })
      .addCase(register.rejected, (state) => {
        state.loading = false;
      })
      .addCase(login.pending, (state) => {
        state.loading = true;
      })
      .addCase(login.fulfilled, (state) => {
        state.loading = false;
        state.isAuthenticated = true;
      })
      .addCase(login.rejected, (state) => {
        state.loading = false;
      })
      .addCase(getUser.pending, (state) => {
        state.loading = true;
      })
      .addCase(getUser.fulfilled, (state, action) => {
        state.loading = false;
        state.user = action.payload;
        state.isAuthenticated = true;
      })
      .addCase(getUser.rejected, (state) => {
        state.loading = false;
      });
  },
});
export const { resetRegistered, logout } = userSlice.actions;
export default userSlice.reducer;
