import React, { useState, useEffect } from "react";
import { styled } from "@mui/material/styles";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell, { tableCellClasses } from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";
import { Button } from "@mui/material";
import SendIcon from "@mui/icons-material/Send";
import { useSelector } from "react-redux";
import { getCookie } from "../CookieHandler";
import IssuedBooks from "./IssuedBooks";

const StyledTableCell = styled(TableCell)(({ theme }) => ({
  [`&.${tableCellClasses.head}`]: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
  [`&.${tableCellClasses.body}`]: {
    fontSize: 17,
  },
}));

const StyledTableRow = styled(TableRow)(({ theme }) => ({
  "&:nth-of-type(odd)": {
    backgroundColor: theme.palette.action.hover,
  },
  "&:last-child td, &:last-child th": {
    border: 0,
  },
}));

function createData(book, category) {
  return { book, category };
}

//function started from here
export default function Library(props) {
  const { user } = useSelector((state) => state.user);
  const [books, setBooks] = useState([]);

  const getDataFromServer = () => {
    const access = getCookie("access");

    fetch(window.configuration.API_URL + "data_api/booklist", {
      method: "GET",
      headers: {
        Accept: "application/json",
        Authorization: `Bearer ${access}`,
      },
    })
      .then((response) => response.json())
      .then((responseData) => {
        console.log(responseData);//getting array of objects with key= book and category
        if (responseData.success && responseData.success == 1) {
          setBooks(responseData.books);
        }
      })
      .catch((error) => console.log(error));
  };

  useEffect(() => {
    getDataFromServer();
  }, []);

  const onIssueButtonClick = (row) => {
    const access = getCookie("access");
    fetch(window.configuration.API_URL + "data_api/issuebook", {
      method: "POST",
      headers: {
        Accept: "application/json",
        Authorization: `Bearer ${access}`,
      },
      body: JSON.stringify({ user_email: user.email, book_name: row.book }),
    })
      .then((response) => response.json())
      .then((data) => {
        props.setIssueBookTrigger(prev=>prev+1)
      })
      .catch((error) => console.log(error));
  };

  const rows = books.map((item) => {
    return createData(item.book, item.category);
  });

  return (
    <>
      <TableContainer component={Paper} className="container p-0 " style={{maxHeight:'80vh', overflow:'auto'}}>
        <Table sx={{ minWidth: 700 }} aria-label="customized table">
          <TableHead>
            <TableRow>
              <StyledTableCell>Book</StyledTableCell>
              <StyledTableCell>Category</StyledTableCell>
              <StyledTableCell>Issue Book</StyledTableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {rows.map((row) => (
              <StyledTableRow key={row.book}>
                <StyledTableCell component="th" scope="row">
                  {row.book}
                </StyledTableCell>
                <StyledTableCell>{row.category}</StyledTableCell>
                <StyledTableCell>
                  <Button
                    variant="outlined"
                    endIcon={<SendIcon style={{ fontSize: "13px" }} />}
                    onClick={() => {
                      onIssueButtonClick({
                        book: row.book,
                      });
                    }}
                  >
                    Issue
                  </Button>
                </StyledTableCell>
              </StyledTableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </>
  );
}
