import React, { useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { getCookie } from '../CookieHandler';

export default function IssuedBooks(props) {
    const {isAuthenticated, user} = useSelector((state) => state.user);
    const [issuedBooksList, setIssuedBooksList] = useState([])
    
    const fetchIssuedBooks =()=>{
        if(user){
            const access = getCookie('access')
            fetch(window.configuration.API_URL + "data_api/getissuedbooks", {
              method: "POST",
              headers: {
                Accept:'application/json',
                Authorization:`Bearer ${access}`
              },
              body: JSON.stringify({user_email:user.email }),
            })
              .then((response) => response.json())
              .then((data) => {
                setIssuedBooksList(data.issued_books_list)
                // alert(JSON.stringify(data))
                console.log('issuedbookslist',issuedBooksList)
              })
              .catch((error) => console.log(error));
        }
    }

    const issueBookTrigger = props.issueBookTrigger
    useEffect (
        ()=>{
            fetchIssuedBooks()
        },[user, issueBookTrigger]
    )

  return (
    <div className='mt-2 mx-5 mb-5 rounded border shadow d-flex align-items-center justify-content-center' style={{backgroundColor:'rgba(47,90,210,0.5)',width:'380px',height:'600px'}} >
        <div>
        {issuedBooksList && issuedBooksList.length > 0
        ?<div>
            <h1>Issued Books:</h1>
            
            <div>
                {issuedBooksList.map((book)=>{
                    return <div><li>{book}</li></div>
                })}
            </div>
        </div>
        :<h1>No issued books!!</h1>}
        </div>
        
    </div>
  );
}
