import React from "react";
import {
  Grid,
  Paper,
  Avatar,
  Typography,
  TextField,
  Button,
} from "@mui/material";
import AddCircleOutlineOutlinedIcon from "@mui/icons-material/AddCircleOutlineOutlined";
import { Formik, Form, Field, ErrorMessage } from "formik";
import * as Yup from "yup";
import { useDispatch, useSelector } from "react-redux";
import { register } from "../ReduxSlices/userSlice";
import { Navigate } from "react-router-dom";

const Signup = (props) => {
  const { loading, registered } = useSelector((state) => state.user);
  const dispatch = useDispatch();

  const paperStyle = { padding: 20, width: 300, margin: "0 auto" };
  const headerStyle = { margin: 0 };
  const avatarStyle = { backgroundColor: "#1bbd7e" };
  const btnstyle = { margin: "8px 0" };

  const initialValues = {
    name: "",
    phone: "",
    email: "",
    password: "",
    address: "",
    college: "",
  };

  const validationSchema = Yup.object().shape({
    name: Yup.string().required("Required"),
    phone: Yup.string().required("Required"),
    address: Yup.string().required("Required"),
    college: Yup.string().required("Required"),
    email: Yup.string().email("please enter valid email").required("Required"),
    password: Yup.string()
      .required("Required")
      .min(8, "minimum 8 characters required!"),
  });

  const onSubmit = (values, props) => {
    dispatch(
      register({
        name: values.name,
        phone: values.phone,
        address: values.address,
        college: values.college, 
        email: values.email,
        password: values.password,
      })
    );
    setTimeout(() => {
        props.setFormType("login")
        console.log('setForm',props.formType)
    }, 500);

  };

  return (
    // registered?(
    //   <Navigate to ="/login"></Navigate>
    // ):
    <Grid>
      <Paper style={paperStyle}>
        <Grid align="center">
          <Avatar style={avatarStyle}>
            <AddCircleOutlineOutlinedIcon />
          </Avatar>{JSON.stringify(registered)}
          <h2 style={headerStyle}>Sign Up</h2>
          <Typography variant="caption" gutterBottom>
            Please fill this form to create an account !
          </Typography>
        </Grid>
        <Formik
          initialValues={initialValues}
          onSubmit={onSubmit}
          validationSchema={validationSchema}
        >
          {(props) => (
            <Form>
              <Field
                as={TextField}
                label="Name"
                name="name"
                placeholder="Enter your Name"
                fullWidth
                required
                helperText={<ErrorMessage name="name" />}
              />
              <Field
                as={TextField}
                label="Phone Number"
                name="phone"
                placeholder="Enter your Phone Number"
                fullWidth
                required
                helperText={<ErrorMessage name="phone" />}
              />
              <Field
                as={TextField}
                label="Email"
                name="email"
                placeholder="Enter Email"
                fullWidth
                required
                helperText={<ErrorMessage name="email" />}
              />
              <Field
                as={TextField}
                label="Password"
                name="password"
                placeholder="Enter Password"
                type="password"
                fullWidth
                required
                helperText={<ErrorMessage name="password" />}
              />
              <Field
                as={TextField}
                label="Address"
                name="address"
                placeholder="Enter your Address"
                fullWidth
                required
                helperText={<ErrorMessage name="address" />}
              />
               <Field
                as={TextField}
                label="College Name"
                name="college"
                placeholder="Enter your College Name"
                fullWidth
                required
                helperText={<ErrorMessage name="college" />}
              />
              <Button
                type="submit"
                color="primary"
                variant="contained"
                disabled={loading}
                style={btnstyle}
                fullWidth
              >
                {loading ? "Loading" : "Sign up"}
              </Button>
            </Form>
          )}
        </Formik>
      </Paper>
    </Grid>
  );
};

export default Signup;
