import React,{useState} from 'react';
import {Grid} from '@mui/material'
import BookList from './BookList'
import IssuedBooks from './IssuedBooks';

const Dashboard = () => {
  
    const [issueBookTrigger, setIssueBookTrigger] = useState(0)

  return (
    <div className='container p-4 '>
        <Grid container spacing={2}>
        <Grid item xs={8}>
            <BookList setIssueBookTrigger={setIssueBookTrigger} />
        </Grid>
        <Grid item xs={4}>
            <IssuedBooks issueBookTrigger={issueBookTrigger}/>
        </Grid>
    </Grid>
    </div>
    
  );
}

export default Dashboard;
